<template>
  <el-container>
    <el-header>
      <header-screen></header-screen>
    </el-header>
    <el-container>
      <el-main>
        <build-thumbnails></build-thumbnails>
      </el-main>
      <el-aside width="200px">Aside</el-aside>
    </el-container>
  </el-container>
</template>

<script>
import headerScreen from "../../components/headerScreen";
import buildThumbnails from "./buildThumbnails";
export default {
  components: {
    headerScreen,
    buildThumbnails,
  },
};
</script>

<style scoped>
.el-header {
  background-color: #204d90;
  color: #fff;
  text-align: center;
  line-height: 60px;
}

.el-aside {
  background-color: #d3dce6;
  color: #333;
  text-align: center;
  line-height: 200px;
}

.el-main {
  background-color: #e9eef3;
  color: #333;
  text-align: center;
  line-height: 160px;
}

body > .el-container {
  margin-bottom: 40px;
}

.el-container:nth-child(5) .el-aside,
.el-container:nth-child(6) .el-aside {
  line-height: 260px;
}

.el-container:nth-child(7) .el-aside {
  line-height: 320px;
}
</style>


